import React, { useState } from 'react'
import { AppBar, Toolbar, Tabs, Tab } from "@mui/material";
import { Link } from 'react-router-dom'
import { Container, Row, Col } from 'react-bootstrap';
import { HashLink } from 'react-router-hash-link';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';

function NavTab() {
    const [value, setValue] = useState();

    return (
        <>
            <Navbar className="nav-bg-color" expand="lg">
                <Container>
                    <Navbar.Brand className="logo-name" href="Portfolio"><h1>Portfolio</h1></Navbar.Brand>
                    <Navbar.Toggle aria-controls="basic-navbar-nav" />
                    <Navbar.Collapse id="basic-navbar-nav" className='justify-content-end'>
                        <Nav >
                            <Nav.Link className="link-name" href="/Portfolio" >Home</Nav.Link>
                            <Nav.Link className="link-name" href="/Experience">Experience</Nav.Link>
                            <Nav.Link className="link-name" href="/Project">Project</Nav.Link>
                            <Nav.Link className="link-name" href="/Contact_us" >Contact</Nav.Link>
                            <Nav.Link className="link-name" href="/about" >About</Nav.Link>

                        </Nav>
                    </Navbar.Collapse>
                </Container>
            </Navbar>

        </>

    );
}

export default NavTab;